/*
 * WooCommerce Fluid Checkout layout styles
 */

.wfc-header {
  display: none;
}

#wfc-wrapper.active .wfc-header {
  display: block;
}



#wfc-progressbar {
  margin: 0;
  overflow: hidden;
}
.wfc-step {
  display: inline-block;
  padding: .5rem 2rem;
  box-sizing: border-box;
  position: relative;
  cursor: pointer;
}
@supports ( display: flex ) {
  #wfc-progressbar {
    display: flex;
  }
  .wfc-step {
    flex: 1;
    padding: 0;
  }
}


.wfc-step[disabled],
.wfc-step.disabled {
  cursor: default;
}

.wfc-step:first-child:before,
.wfc-step:last-child:after {
  content: '';
  display: none;
}



.wfc-header {
  display: block;
}

#wfc-wrapper.active .wfc-frame {
  display: none;
}

#wfc-wrapper.active .wfc-frame.current {
  display: block;
}



#wfc-wrapper.active .wfc-actions {
  text-align: center;
}
@supports ( display: flex ) {
  #wfc-wrapper.active .wfc-actions {
    display: flex;
    width: 100%;
  }
}



#wfc-wrapper .wfc-prev,
#wfc-wrapper .wfc-next {
  display: none;
}

#wfc-wrapper.active .wfc-prev,
#wfc-wrapper.active .wfc-next {
  width: auto;
  display: inline-block;
}

#wfc-wrapper.active .wfc-next {
  min-width: 22rem;
}

@supports ( display: flex ) {
  #wfc-wrapper.active .wfc-prev {
    flex-shrink: 1;
  }

  #wfc-wrapper.active .wfc-next {
    flex-grow: 1;
    flex-shrink: 1;
  }
}
